import {mapMutations} from 'vuex'
export default {
	data() {
		return {
			shopDetail: {}
		}
	},
	onLoad(options) {
		this.shopDetail = JSON.parse(options.shopItem)
	},
	methods:{
		...mapMutations({
			addShopItemMutation: 'addShopItemMutation'
		}),
		// 点击添加至购物车的回调
		addShopCart(){
			// shopDetail 商品信息对象
			console.log('添加至购物车')
			this.addShopItemMutation(this.shopDetail)
		}
	},
	
}
